<template>
  <div class="thumbnail">
      <card>
          <slot></slot>
          <div class="thumbnail-container">
              <p class="title">{{title}}</p>
              <p class="description">{{description}}</p>
              <div class="thumbnail-container-btns">
                <a :href="infoHref"><a-button>About</a-button></a>
                <a :href="playHref"><a-button btn-type="primary">Play</a-button></a>
              </div>
              
          </div>
          
         
      </card>
  </div>
</template>

<script>
import Card from '../../common/Card/Card'
import AButton from '../../common/AButton/AButton'
export default {
    name:'Thumbnail',
    components:{
        Card,AButton
    },
    props:{
        title:{
            type:String,
            required:true
        },
        description:{
            type:String,
            required:true
        },
        infoHref:{
            type:String,
            default:''
        },
        playHref:{
            type:String,
            default:''
        }
    }
}
</script>

<style>
.thumbnail{
    width:250px;
}
.thumbnail img{
    width:100%;
}
.thumbnail .thumbnail-container{
    padding: 5px 10px;
}
.thumbnail p{
    margin:10px 0;
}
.thumbnail .title{
    font-weight: bold;
    font-size: 16px;
}
.thumbnail .description{
    color: rgba(0, 0, 0, 0.65);
    font-size: 14px;
}
.thumbnail .thumbnail-container-btns{
    margin-top: 20px;
    margin-bottom: 10px;
    display: flex;
    justify-content: flex-end;
}
.thumbnail button{
    margin: 0 5px;
}
</style>